* {
  font-family: "Montserrat", sans-serif;
  color: inherit;
  text-decoration: none;
  list-style: none;
}

a,
a:hover,
a:active,
a:focus,
a:visited {
  outline: none;
}

body {
  background: #f7f7f7;
  overflow-x: hidden;
}

html {
  overflow-x: hidden;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  flex-grow: 1;
}

.section-title {
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  width: 100%;
  flex-basis: 100%;
}

.primary-button {
  border: 2px solid #e7426a;
  background: #e7426a;
  border-radius: 3px;
  color: #fff;
  position: relative;
  z-index: 1;
  padding: 11px 16px;
  font-size: 14px;
  font-weight: 500;
}
.primary-button:hover {
  background: none;
  transition: 0.3s;
  color: #e7426a;
}

.links {
  text-align: center;
  padding-top: 41px;
}
.links .link {
  color: rgb(41, 41, 41);
}
.links .link.active {
  color: #4e9dd3;
}

.topswiper {
  background: #d4e9f9;
  font-size: 18px;
}
.topswiper__inner {
  padding-block: 5px;
}
.topswiper__container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.topswiper .swiper-wrapper {
  align-items: center;
}
.topswiper__btn {
  border: none;
  background: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5000;
}
.topswiper__btn-prev {
  left: 0;
}
.topswiper__btn-next {
  right: 0;
}

.header {
  background: #f7ebe5;
}
.header__row {
  padding-block: 12px;
  align-items: center;
}
.header__bottom {
  background: #fff;
}
.header .nav__list {
  display: flex;
  gap: 14px;
  font-size: 14px;
}
.header__content {
  display: flex;
  justify-content: space-between;
}
.header__city, .header__phone, .header__basket {
  display: flex;
  align-items: center;
  gap: 11px;
}
.header__city {
  width: 25%;
  margin-left: 30px;
}
.header__social {
  width: 10%;
}
.header__social svg {
  cursor: pointer;
}
.header__select:hover > .select__collapse {
  visibility: visible;
  opacity: 1;
  transition: 0.1s;
  transform: translateY(0);
}
@keyframes listArrowAnim {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(3px);
  }
  100% {
    transform: translateY(0);
  }
}
.header__select:hover > .select__icon {
  animation: listArrowAnim 0.5s;
}
.header__select::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  top: 16px;
  right: 0;
}
.header__select::before:hover + .select__collapse {
  visibility: visible;
  opacity: 1;
  transition: 0.1s;
  transform: translateY(0);
}
.header .select__collapse {
  position: absolute;
  top: 30px;
  right: 0;
  background: #fff;
  width: 270px;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: 0.1s;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.15);
  transform: translateY(-10px);
}
.header .select__item {
  padding: 20px 16px;
  cursor: pointer;
  position: relative;
  z-index: 0;
}
.header .select__item:hover {
  background: #f7f7f7;
}
.header .city-collapse {
  background: #f7ebe5;
  width: 200px;
  left: 0;
}
.header .city-select:hover {
  background: #ecc7b4;
}
.header__select {
  display: flex;
  align-items: center;
  gap: 5px;
  user-select: none;
  cursor: pointer;
  position: relative;
}
.header__social {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav {
  font-size: 14px;
  text-transform: uppercase;
}
.header__nav-list {
  display: flex;
  align-items: center;
  gap: 30px;
}
.header__nav-item {
  display: flex;
  align-items: center;
  gap: 5px;
  position: relative;
}

