  flex-direction: column;
  text-align: center;
  padding-block: 36px;
  position: relative;
  gap: 20px;
}
.cards-links__inner:hover > .cards-links__title {
  transform: scale(1.1);
  transition: 0.1s;
}
.cards-links__inner:hover > .cards-links__svg {
  transition: 0.1s;
  transform: scale(1.1);
}
.cards-links__inner:hover > .cards-links__text {
  transition: 0.1s;
  transform: scale(1.1);
}
.cards-links__inner::before {
  content: "";
  position: absolute;
  border: 0 solid transparent;
  z-index: 0;
}
.cards-links__text, .cards-links__title, .cards-links__svg {
  transition: 0.1s;
}
.cards-links__title {
  font-size: 20px;
  font-weight: 600;
  position: relative;
  z-index: 1;
}
.cards-links__title svg {
  margin-left: 5px;
  transition: 0.1s;
  transform: scale(1);
}
